.title {
    text-transform: uppercase;
    font-size: 50px;
    font-family: "Montserrat", sans-serif;
    cursor: pointer;
    color: grey;
}

.header {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.divider {
    margin: 6px;
    width: 100%;
    background-color: grey;
}
.quiz {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: "Poppins", sans-serif;
    margin-left: 300px;
    margin-right: 300px;
}

.subtitle {
    font-size: 25px;
    border: 1px solid black;
    box-shadow: 4px 4px 2px black;
    padding: 5px 10px;
}

.quizInfo {
    width: 100%;
    display: flex;
    justify-content: space-between;
    text-transform: uppercase;
    margin: 10px;
}
.question {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.singleQuestion {
    width: 100%;
    min-height: 350px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    border: 5px solid grey;
    padding: 20px;
    margin-top: 10px;
}

.options {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex: 1 1;
    align-items: center;
    justify-content: space-around;
    margin: 10px;
}

.singleOption {
    width: 46%;
    height: 50px;
    padding: 15px 20px;
    margin: 10px;
    box-shadow:  0 0 2px black;
}

@media (max-width: 614px) {
    .singleOption {
        width: 100%;
    }
}

.select {
    background-color: rgb(7, 207, 0);
    color: white;
    box-shadow: 0 0 1px black;
}

.wrong {
    background-color: rgb(233, 0, 0);
    color: white;
    box-shadow: 0 0 1px black;
}

.controls {
    display: flex;
    width: 100%;
    justify-content: space-around;
}
.result {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 60vh;
    text-align: center;
    margin-left: 500px;
    margin-right: 500px;
}


.cat {
    margin-right: 500px;
    margin-left: 500px;
}

.cate {
    margin-top: 20px;
    width: 50%;
    float: left;
    padding: 20px;
    font-size: 30px;
    border: 2px solid black;
}  

#a {
    display: block;
    
}



* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Poppins", sans-serif;
}

body {
    min-height: 100vh;
    background-size: cover;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.contact-section {
    margin-right: 300px;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.contact-info {
    padding-left: 50px;
    color: black;
    max-width: 500px;
    line-height: 65px;
    padding-top: 50px;
    font-size: 18px;
    padding-bottom: 30px;
}

.contact-info i {
    margin-right: 20px;
    font-size: 25px;
}

.contact-form {
    padding-top: 10px;
    padding-right: 50px;
    max-width: 2500px;
}

.contact-info, .contact-form {
    flex: 1 1;
}

.contact-form h2 {
    color: rgb(10, 1, 22);
    text-align: center;
    font-size: 35px;
    text-transform: uppercase;
    margin-bottom: 30px;
}

.contact-form .text-box {
    background: #050505;
    color: rgb(196, 202, 214);
    border: none;
    width: calc(50% - 10px);
    height: 50px;
    padding: 12px;
    font-size: 15px;
    border-radius: 5px;
    box-shadow: 0 1px 1px rgba(240, 226, 226, 0.1);
    margin-bottom: 20px;
    opacity: 0.9;
}

.contact-form .text-box:first-child {
    margin-right: 15px;
}

.contact-form textarea {
    background: #050505;
    color: rgb(240, 242, 247);
    border: none;
    width: 100%;
    padding: 12px;
    font-size: 15px;
    min-height: 200px;
    max-height: 400px;
    resize: vertical;
    border-radius: 5px;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
    margin-bottom: 20px;
    opacity: 0.9;
}

.contact-form .send-btn {
    float: right;
    background: rgb(10, 1, 22);
    color: #fff;
    border: none;
    width: 120px;
    height: 40px;
    font-size: 15px;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 2px;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s;
    transition-property: background;
}

.contact-form .send-btn:hover {
    background: #0582E3;
}

@media screen and (max-width: 950px){
    .contact-section {
        flex-direction: column;
    }
    .contact-info, .contact-form {
        margin: 30px 50px;
    }
    .contact-form h2 {
        font-size: 30px;
    }
    .contact-form .text-box {
        width: 100%;
    }
}

.alert-success {
    z-index: 1;
    background: #D4EDDA;
    font-size: 18px;
    padding: 20px 40px;
    min-width: 420px;
    position: fixed;
    right: 0;
    top: 10px;
    border-left: 8px solid #3AD66E;
    border-radius: 4px;
}

.alert-error {
    z-index: 1;
    background: #FFF3CD;
    font-size: 18px;
    padding: 20px 40px;
    min-width: 420px;
    position: fixed;
    right: 0;
    top: 10px;
    border-left: 8px solid #FFA502;
    border-radius: 4px;
}
